import { createRouter, createWebHistory, RouteRecordRaw } from "vue-router";
import Home from "../pages/Home.vue"
import Layout from "@/pages/layout/Layout.vue"

const routes: Array<RouteRecordRaw> = [
  {
    path: "",
    name: "",
    component: Layout,
    children: [
      {
        path: '',
        name: 'home',
        component: Home,
        meta: {
          title: ''
        }
      },
      {
        path: '/preview',
        name: 'preview',
        component: () => import('@/pages/Preview.vue'),
        meta: {
          title: ''
        }
      },
      {
        path: '/trend',
        name: 'trend',
        component: () => import('@/pages/Trend.vue'),
        meta: {
          title: ''
        }
      },
      {
        path: '/foottoolbar',
        name: 'foottoolbar',
        component: () => import('@/pages/FootToolBar.vue'),
        meta: {
          title: ''
        }
      },
      {
        path: '/noticeicon',
        name: 'noticeicon',
        component: () => import('@/pages/NoticeIcon.vue'),
        meta: {
          title: ''
        }
      },
      {
        path: '/iframe',
        name: 'iframe',
        component: () => import('@/pages/IframeView.vue'),
        meta: {
          title: ''
        }
      },
      {
        path: '/countdown',
        name: 'countdown',
        component: () => import('@/pages/CountDown.vue'),
        meta: {
          title: ''
        }
      },
      {
        path: '/progress',
        name: 'progress',
        component: () => import('@/pages/AiProgress.vue'),
        meta: {
          title: ''
        }
      },
      {
        path: '/qrcode',
        name: 'qrcode',
        component: () => import('@/pages/Qrcode.vue'),
        meta: {
          title: ''
        }
      },
      {
        path: '/copy',
        name: 'copy',
        component: () => import('@/pages/Copy.vue'),
        meta: {
          title: ''
        }
      },
      {
        path: '/watermark',
        name: 'watermark',
        component: () => import('@/pages/Watermark.vue'),
        meta: {
          title: ''
        }
      },
      {
        path: '/edittext',
        name: 'edittext',
        component: () => import('@/pages/EditText.vue'),
        meta: {
          title: ''
        }
      },
      {
        path: '/hooks',
        name: 'hooks',
        component: () => import('@/pages/Hooks.vue'),
        meta: {
          title: 'hooks'
        }
      }
    ]
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to, from, next) => {
  document.title = to.meta.title
  next()
})

export default router;
